.post {
  border-color: rgb(125, 125, 125);
  padding: 5px;
  border-top-style: solid;
  border-right-style: solid;
  border-left-style: solid;
  border-width: 1px;
  background-color: rgb(31, 31, 31);
  min-width: 700px;
  max-width: 700px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  /* flex-wrap: wrap; */
